<template>
	<view class="box">
		<view class="title">
			请填写投诉原因
		</view>

		<view class="reason" v-for="(val,k,i) in reason_list" :key="k" @tap="chooseReason(k)">
			{{val}}
			<text class="check" v-show="k==reason_id">√</text>
		</view>
		<view class="">
			<textarea class="complaint" v-model="complaint_content" value="" placeholder="请输入投诉描述" />
			</view>
		<button type="primary" :loading="load" @tap="submit">提交</button>
	</view>
</template>

<script>
	let articleSeqId="";
	let articleTitle=""
	export default {
		data() {
			return {
				reason_list:"",
				reason_id:"",
				complaint_content:"",
				load:false,
				
			}
		},
		onLoad(option) {
			articleSeqId=option.articleSeqId
			articleTitle=option.articleTitle
			this.getComplaint()
		},
		methods: {
			getComplaint(){
				this.$api.get('/o2oArticleComplaint/getComplaintReason').then(res=>{
					if(res.code==200){
						this.reason_list=res.data;
					}
				})
			},
			submit(){
				if(this.load)return;
				if(!this.reason_id){
					this.$box.toast('请选择投诉原因','none');
					return
				}
				if(!this.complaint_content){
					this.$box.toast('请输入投诉描述','none');
					return
				}
				this.load=true;
				this.$api.post('/o2oArticleComplaint/insert',{
					"articleSeqId":articleSeqId,
					"articleTitle": articleTitle,
					"complaintContent": this.complaint_content,
					"complaintReason": "R"+this.reason_id,
					"customerSeqId": this.$common.getLocalSync('customerSeqId')
				}).then(res=>{
					this.load=false;
					if(res.code==200){
						this.$box.toast('提交成功')
					}
				})
			},
			chooseReason(k){
				this.reason_id=k;
			}
		}
	}
</script>

<style lang="scss">
	.box {
		width: calc(100% - 60rpx);
		padding: 0 30rpx;
		padding-bottom: 100rpx;
		background: white;
		.title {
			font-size: 26rpx;
			color: #535353;
			border-bottom:1px solid #f3f3f3;
			height: 64rpx;
			line-height: 64rpx;
			width: 100%;
		}
		.reason {
			height: 60rpx;
			line-height: 60rpx;
			color: #515151;
			font-size: 22rpx;
			border-bottom:1px solid #f3f3f3;
			display: flex;
			justify-content: space-between;
			.check{
				color: $color-primary;
				font-weight: bolder;
				font-size: 30rpx;
				padding-right: 20rpx;
			}
		}
		.complaint {
			width: calc(100% - 40rpx);
			height: 170rpx;
			font-size: 22rpx;
			margin: 40rpx auto;
			background: #f8faf9;
			border: 1px solid #eeeeee;
			padding: 20rpx;
		}
		
	}

</style>
